    icoHardCapUsd.mask('000 000 000 000 000 000', { reverse: true });

    var currentPeriod__input = $('#currentPeriod__input');
    var currentPeriod__val = currentPeriod__input.val();
    var currentPeriod = $('.currentPeriod__FORMATTED');
